import React, { useState,useEffect } from 'react'
import './BoardAdsBar.scss'
import Arms from '../../assets/svg/armsIcon.svg'
import SvgColor from 'react-svg-color'
import CarIcon from '../../assets/svg/car.svg'
import House from '../../assets/svg/house.svg'
import people from '../../assets/svg/people.svg'
import work from '../../assets/svg/work.svg'
import newIcon from '../../assets/svg/new.svg'
import favorites from '../../assets/svg/favorites.svg'
import DropdownCity from '../DropdownCity/DropdownCity'
import { Link,useParams } from 'react-router-dom'
import NavDropdown from "../NavDropdown/NavDropdown";
import * as categoryArr from '../../assets/json/category.json'
import {changeSelectedCategory,changeSelectedSubcategory } from '../../redux/actions/dropdownAction'
export default function BoardAdsBar() {
  const [category, setCategory ] = useState('');
  const { nav } = useParams();

  //dropdown section(category)
  const stateSection = JSON.parse(JSON.stringify(categoryArr.default))
  const optionsSection = Object.keys(stateSection).map((item, i) => {
    return { key: i, text: item, value: item }
  })
  const [ subsection, setOptionsSubsection ] = useState([])
  const [ selectedSection, setSelectedSection ] = useState('')
  useEffect(() => {
    selectedSection && setOptionsSubsection(stateSection[selectedSection].map((item, i) => {
      return { key: i, text: item, value: item }
    }))
  }, [selectedSection])
  // end logic dropdown section

  const [selectedSubsection, setSelectedSubsection ] = useState('')


  useEffect(() => {
    if(nav === 'saleBuy') setCategory('/')
    if(nav === 'property') setCategory('')
    if(nav === 'auto') setCategory('')
    if(nav === 'services') setCategory('')
    if(nav === 'work') setCategory('')
    if(nav === 'newAuto') setCategory(' ')
    if(nav === 'newHouse') setCategory(' ')
    if(nav === 'favorites') setCategory('favorites')
  }, [nav])

  

 


  return (
    <ul className='boardAdsBar boardAdsBar--mt'>
      <li className={category === '/' ? 'boardAdsBar__item boardAdsBar__item--active boardAdsBar__item--saleBuy' : 'boardAdsBar__item boardAdsBar__item--saleBuy'}>
        <Link to='/home/saleBuy' style={{ color: '#000' }}>
          <div className="boardAdsBar__categoryContainer">
            <SvgColor svg={Arms} colors={category === '/' ? ['#fff'] : ['#616C7A']} />
            <span className='boardAdsBar__text' style={category === '/' ? {color: '#fff'} : null}>/</span>
          </div>
        </Link>
        <ul className="boardAdsBar__subMenu">
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--titleMargin" ></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--cityMargin">
            <DropdownCity />
          </li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--categoryMargin">
            <NavDropdown className='offerForm__dropdown' arr={optionsSection} placeholder='' onChange={(e) => setSelectedSection(e.target.innerText)} action={changeSelectedCategory}/>
          </li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--subcategoryMargin">
            <NavDropdown placeholder='' arr={selectedSection && subsection} onChange={(e) => setSelectedSubsection(e.target.innerText)} action={changeSelectedSubcategory}/>
          </li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--priceMargin"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--photoMargin"></li>
          <li className="boardAdsBar__subMenuItem"></li>
        </ul>
      </li>
      <li className={category === '' ? 'boardAdsBar__item boardAdsBar__item--property boardAdsBar__item--active' : 'boardAdsBar__item boardAdsBar__item--property'} >
        <Link to='/home/property' style={{ color: '#000' }}>
          <div className="boardAdsBar__categoryContainer">
            <SvgColor svg={House} colors={category === '' ? ['#fff'] : ['#616C7A']} />
            <span className='boardAdsBar__text' style={category === '' ? {color: '#fff'} : null}></span>
          </div>
        </Link>
        <ul className="boardAdsBar__subMenu">
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--titleProperty"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--cityMargin">
            <DropdownCity />
          </li>
    
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--categoryProperty"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--subcategoryProperty"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--squareProperty"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--floarMargin"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--roomsMargin"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--roomsMargin"> </li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--priceMargin"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--photoMargin"></li>
          <li className="boardAdsBar__subMenuItem"></li>
        </ul>
      </li>
      <li className={category === '' ? 'boardAdsBar__item boardAdsBar__item--auto boardAdsBar__item--active' : 'boardAdsBar__item boardAdsBar__item--auto'}>
        <Link to='/home/auto' style={{ color: '#000' }}>
          <div className="boardAdsBar__categoryContainer">
            <SvgColor svg={CarIcon} colors={category === '' ? ['#fff'] : ['#616C7A']} />
            <span className='boardAdsBar__text'style={category === '' ? {color: '#fff'} : null}> </span>
          </div>
        </Link>
        <ul className="boardAdsBar__subMenu">
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--titleProperty"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--cityMargin"><DropdownCity /></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--autoCategory"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--brand"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--model"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--yearOfIssue">..</li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--yearOfIssue"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--yearOfIssue"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--yearOfIssue">..</li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--priceMargin"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--photoMargin"></li>
          <li className="boardAdsBar__subMenuItem"></li>
          
        </ul>
      </li>
      <li className={category === '' ? 'boardAdsBar__item boardAdsBar__item--services boardAdsBar__item--active' : 'boardAdsBar__item boardAdsBar__item--services'} >
        <Link to='/home/services' style={{ color: '#000' }}>
          <div className="boardAdsBar__categoryContainer">
            <SvgColor svg={people} colors={category === '' ? ['#fff'] : ['#616C7A']} />
            <span className='boardAdsBar__text' style={category === '' ? {color: '#fff'} : null}></span>
          </div>
        </Link>
        <ul className="boardAdsBar__subMenu">
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--titleProperty"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--cityServices">
            <DropdownCity />
          </li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--categoryMargin"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--subcategoryServices"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--priceMargin"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--photoMargin"></li>
          <li className="boardAdsBar__subMenuItem"></li>
        </ul>
      </li>
      <li className={category === '' ? 'boardAdsBar__item boardAdsBar__item--work boardAdsBar__item--active' : 'boardAdsBar__item boardAdsBar__item--work'}>
        <Link to='/home/work' style={{ color: '#000' }}>
          <div className="boardAdsBar__categoryContainer">
            <SvgColor svg={work} colors={category === '' ? ['#fff'] : ['#616C7A']} alt="," />
            <span className='boardAdsBar__text' style={category === '' ? {color: '#fff'} : null}></span>
          </div>
        </Link>
        <ul className="boardAdsBar__subMenu">
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--titleMargin"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--cityMargin">
            <DropdownCity />
          </li>
        
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--categoryMargin"> </li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--subcategoryMargin"> </li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--priceMargin"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--photoMargin"></li>
          <li className="boardAdsBar__subMenuItem"></li>
        </ul>
      </li>
      <li className={category === ' ' ? 'boardAdsBar__item boardAdsBar__item--newAuto boardAdsBar__item--active' : 'boardAdsBar__item boardAdsBar__item--newAuto'}>
        <Link to='/home/newAuto' style={{ color: '#000' }}>
          <div className="boardAdsBar__categoryContainer">
            <SvgColor svg={newIcon} colors={category === ' ' ? ['#fff'] : ['#616C7A']} />
            <span className='boardAdsBar__text' style={category === ' ' ? {color: '#fff'} : null}> </span>
          </div>
        </Link>
        <ul className="boardAdsBar__subMenu">
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--titleMargin"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--cityMargin"><DropdownCity /></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--autoCategory"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--yearOfIssue"> </li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--brand"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--model"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--priceMargin"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--photoMargin"></li>
          <li className="boardAdsBar__subMenuItem"></li>
        </ul>
      </li>
      <li className={category === ' ' ? 'boardAdsBar__item boardAdsBar__item--newHouse boardAdsBar__item--active' : 'boardAdsBar__item boardAdsBar__item--newHouse'}>
        <Link to='/home/newHouse' active style={{ color: '#000' }}>
          <div className="boardAdsBar__categoryContainer">
            <SvgColor svg={newIcon} colors={category === ' ' ? ['#fff'] : ['#616C7A']} />
            <span className='boardAdsBar__text' style={category === ' ' ? {color: '#fff'} : null}> </span>
          </div>
        </Link>
        <ul className="boardAdsBar__subMenu">
        <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--titleProperty"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--cityMargin">
            <DropdownCity />
          </li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--categoryProperty"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--subcategoryProperty"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--squareProperty"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--floarMargin"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--roomsMargin"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--roomsMargin"> </li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--priceMargin"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--photoMargin"></li>
          <li className="boardAdsBar__subMenuItem"></li>
        </ul>
      </li>
      <li className={category === 'favorites' ? 'boardAdsBar__item boardAdsBar__item--favorites boardAdsBar__item--active' : 'boardAdsBar__item boardAdsBar__item--favorites'} >
        <Link to='/home/favorites' active style={{ color: '#000' }}>
          <div className="boardAdsBar__categoryContainer">
            <img src={category === 'favorites' ? favorites : favorites} alt="," />
            <span className='boardAdsBar__text' style={category === 'favorites' ? {color: '#fff'} : null}></span>
          </div>
        </Link>
        <ul className="boardAdsBar__subMenu">
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--titleMargin"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--cityMargin">
            <DropdownCity />
          </li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--categoryMargin"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--subcategoryMargin"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--priceMargin"></li>
          <li className="boardAdsBar__subMenuItem boardAdsBar__subMenuItem--photoMargin"></li>
          <li className="boardAdsBar__subMenuItem"></li>
        </ul>
      </li>
    </ul>
  )
}
